<template>
    <div>
        <v-row style="position: sticky; top: 72px; z-index: 6">
            <v-col cols="12">
                <v-btn outlined text to="/works" exact large><v-icon>chevron_left</v-icon>BACK TO WORKS</v-btn>
            </v-col>
        </v-row>

        <v-row style="height: 70vh" align="center" class="text-center">
            <v-col cols="12">
                <h3 class="display-3 font-weight-bold">{{ title }}</h3>
                <p class="font-weight-thin">{{ quotes[ Math.floor(Math.random() * 5) ] }}</p>
                <v-btn x-large icon class="ma-16"
                       @click="$vuetify.goTo('#content', { offset: 240, easing: 'easeInOutCubic' })">
                    <v-icon class="ic_scroll_down">keyboard_arrow_down</v-icon>
                </v-btn>
            </v-col>
        </v-row>
    </div>
</template>

<script>
    export default {
        name: "LandingComponent",
        props: ["title"],
        data() {
          return {
              quotes: [
                  "scroll down to see stuffs",
                  "going down wont let you down",
                  "something is lurking below",
                  "dive down",
                  "down is up"
              ]
          }
        },
        mounted() {
            window.scrollTo(0 ,0);
            setTimeout(() => {
                (document.getElementById("content") !== null) &&
                    this.$vuetify.goTo('#content', {
                        offset: 240, easing: 'easeInOutCubic'
                    })
            }, 2000)
        }
    }
</script>

<style scoped>
    .ic_scroll_down {
        animation: pulse 2s ease-out;
        animation-iteration-count: infinite;
    }

    .ic_scroll_down:hover {
        transform: rotate(360deg);
    }

    @keyframes pulse {
        0% { font-size: 40px; }
        10% { font-size: 56px; }
        20% { font-size: 40px; }
        100% { font-size: 40px; }
    }
</style>
